import React, { useEffect, useState } from 'react';
import SkyBlueOval from '../Assets/skyblue_oval.png';
const Background =()=>{

    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };

    return(
        <>
        
    {(width > 1200) ?   
        <img className="img-fluid top_carrierapp" style={{justifyContent:'right',top:'2050px'}}  src={SkyBlueOval} alt="" />
       :
        null
    }
    {(width > 1200) ?   
        <img className="img-fluid top_carrierapp" style={{justifyContent:'right',top:'2830px'}}  src={SkyBlueOval} alt="" />
       :
        null
    }


        </>
    )
}
export default Background;