import React, { useEffect, useState } from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Support from "./Support";
import Radium, { StyleRoot } from 'radium';

const AntTabs = withStyles({
  root: {
    borderBottom: '1px solid #e6e6e6',
  },
  indicator: {
    backgroundColor: '#0d55cf',
  },
})(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    minWidth: 72,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(4),
    fontSize: "14px",
    fontFamily: ["cabin"
    ].join(','),
    '&:hover': {
      color: '#0d55cf',
      opacity: 1,
    },
    '&$selected': {
      color: '#0d55cf',
      fontWeight: theme.typography.fontWeightBold,
    },
    '&:focus': {
      color: '#0d55cf',
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.paper,
  },
  demo2: {
    backgroundColor: '#2e1534',
  },
}));

export default function QuickSmmary() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);
  
  const [width, setWidth] = useState(window.innerWidth);

  useEffect(() => {
      window.addEventListener('resize', handleWindowSizeChange);
  });

  const handleWindowSizeChange = () => {
      setWidth(window.innerWidth);
  };

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
  {(width <= 1200) ? <div style={{marginTop:'15px'}}></div> :
  null
  }

      <div className={classes.root}>
        <div className={classes.demo1}>
          <AntTabs value={value} onChange={handleChange} variant="scrollable" aria-label="ant example">
            <AntTab label="OVERVIEW" />
            <AntTab label="SYLLABUS" />
            <AntTab label="SUPPORT" />
            <AntTab label="PLACEMENTS" />
            <AntTab label="LEADERBOARD" />
          </AntTabs>
          <div className="col-md-6 mt-3">
            <Support />
          </div>
        </div>
      </div>
    </>
  );
}
