import React from 'react';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import WrapTablePanels from './WrapTablePanels';

const AntTabs = withStyles({
  root: {
    borderBottom: '1px solid #e6e6e6',
    

  },
  indicator: {
    backgroundColor: '#000000',
  },
})(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    textTransform: 'none',
    minWidth: 50,
    fontWeight: theme.typography.fontWeightRegular,
    marginRight: theme.spacing(3),
    display:'flex',
    fontFamily: 'cabin',
    fontSize:'16px',
    '&:hover': {
      color: '#000000',
      opacity: 1,
    },
    '&$selected': {
      color: '#000000',
      fontWeight: theme.typography.fontWeightMedium,
    },
    '&:focus': {
      color: '#000000',
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  padding: {
    padding: theme.spacing(3),
  },
  demo1: {
    backgroundColor: theme.palette.background.paper,
  }
}));

export default function CustomizedTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root} >
      <div className={classes.demo1}>
        <AntTabs value={value} onChange={handleChange} variant="scrollable" aria-label="ant example" >
          <AntTab label="Mechanical" />
          <AntTab label="Electrical" />
          <AntTab label="Civil" />
          <AntTab label="CS" />
          <AntTab label="Elect" />
        </AntTabs>
        <WrapTablePanels value = {value} index ={0}></WrapTablePanels>
      </div>
    </div>
  );
}

