import React, { useEffect, useState } from 'react';
import Logo from './Assets/skill-lynclogo.png';
import "../node_modules/bootstrap/dist/css/bootstrap.css";
import "../node_modules/bootstrap/dist/js/bootstrap.bundle";
import './App.css';
import Home from "./components/Home";
import TopBarhome from "./components/TopBarhome";
import TopBar from "./components/TopBar";
import Background from "./components/Background";


import CourseConsolidate from "./components/CourseConsolidate";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

function App() {
  
  const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };


  return (
    <div className="App">        
      <TopBarhome />
      <div className="container">
          <Router>
                <nav className="navbar navbar-expand-lg navbar-light bg_light mt-2">
                    <div className="container-fluid container_padding">
                        <Link className="navbar-brand col-md-2 text_left" to="/"><img src={Logo} className="logo_size" alt="skill-lycn"/></Link>
                        <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarNav" aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse col-md-7" id="navbarNav">
                            <ul className="navbar-nav nav_align">
                                <li className="nav-item">
                                    <Link className="nav-link active mt-2" aria-current="page" to="/Courses"><span className="nav_text">Courses</span><sup className="sup_text mt-1">NEW!</sup></Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/Workshops"><span className="nav_text">Workshops</span></Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/Webinars"><span className="nav_text">Webinars</span></Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link" to="/Job-reports"><span className="nav_text">Job-reports</span></Link>
                                </li>
                            </ul>
                        </div>
                        
                        {(width <= 991) ? null :
                            <button type="button" className="btn btn-primary login_button"><span className="">LOGIN</span></button>
                        }
                    </div>
                </nav>
              <Switch>
                <Route path="/Courses">
                  <TopBar/>
                  <CourseConsolidate/>
                </Route>
                <Route path="/">
                    <Background/>
                    <Home />
                </Route>
              </Switch>
            </Router>
      </div>
        </div>
        
  );
}

export default App;
