import React,{ useState } from 'react';

import Radium, { StyleRoot } from 'radium';

const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };
    return (
      <p style={{marginBottom:'0px'}} className="text">
      {isReadMore ? text.slice(0, 180) : text}
      <span style={{cursor: "pointer",color: "#0d55cf",'&:hover': {
                                  color: '#0d47a1',
                              }
  }} onClick={toggleReadMore} className="read-or-hide">
        
        {isReadMore ? "...read more" : " read less"}
      </span>
      </p>
    );
    };

const CourseDelivers = () => {

  const buttondeskhide = {
    '@media (max-width: 1149px)': {
        display:'none'
    }
  }
  const buttonmobilehide = {
    '@media (min-width: 1150px)': {
        display:'none'
    }
  }

    return (
        <StyleRoot>
          <div style={buttondeskhide} >
            <div className="col-md-5">
                <h5 className="text_heading text_left" style={{marginTop:'60px',marginBottom:'0px',padding:'0px'}}>Industry relevant courses delivered by experts</h5>
                <p  className="text_paragraph text_left" style={{margin:'30px 0px 0px 0px'}} >A 6 months program which gives students access 
                to the courses at our state of the art skill centre amnd additioinal 
                courses thyat can be taken in an online mode to guide them on their journey 
                into datascience and data Analytics. laptops can be availl at the additional cost of 18K
                </p>
                </div>
            </div>
            <div style={buttonmobilehide} >
            <h5 className="header" style={{marginTop:'60px',marginBottom:'0px',color:'#0d55cf',}}>Industry relevant courses delivered by experts</h5>
                <p className="smalltext col-12" style={{margin:'30px 0px 0px 0px'}}  >
                <ReadMore  >
                A 6 months program which gives students access 
                to the courses at our state of the art skill centre amnd additioinal 
                courses thyat can be taken in an online mode to guide them on their journey 
                into datascience and data Analytics. laptops can be availl at the additional cost of 18K
                
                </ReadMore  >
                </p>
            </div>

        </StyleRoot>

    )
}
export default CourseDelivers;