import React, { useEffect, useState } from 'react';

  
export default function SupportCard(props) {
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };

    const ReadMore = ({ children }) => {
        const text = children;
        return (
          <p style={{marginBottom:'0px'}} className="text">
          {isReadMore ? text.slice(0, 40) : text}
          {/* <span style={{cursor: "pointer",color: "#0d55cf",'&:hover': {
                                      color: '#0d47a1',
                                  }
      }} onClick={toggleReadMore} className="read-or-hide">
            
            
          </span> */}
          </p>
        );
        };
          
    

    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };

    return (
        <div>
            <div className=" text_left support_pad">
                <button style={{backgroundColor:props.color}} className="icon_btn btn_one">#{props.num}</button>
                    {/* <button className="icon_btn btn_one">#1</button> */}
                    <div className="mt-3 support_groupcall">
                        12x7
                    </div>
                    
                    <div className="mt-2">
                    <div style={{minWidth:'100px'}} class="text_support">
                        <ReadMore>
                            Lorem Ipsum is simply dummy text of the printing and typesetting industry. 
                        </ReadMore>
                    </div>
                </div>
                    <div className="mt-2 knowmore" onClick={toggleReadMore}>
                       {isReadMore ? "Know more" : " Know less"}
                    </div>
                    
                </div>

        </div>
    )
}
