import React,{ useState } from 'react';
import InstructorCards from './InstructorCards';
import Box from '@material-ui/core/Box';
import Radium, { StyleRoot } from 'radium';

const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };
    return (
      <p style={{marginBottom:'0px'}} className="text">
      {isReadMore ? text.slice(0, 180) : text}
      <span style={{cursor: "pointer",color: "#0d55cf",'&:hover': {
                                  color: '#0d47a1',
                              }
  }} onClick={toggleReadMore} className="read-or-hide">
        
        {isReadMore ? "...read more" : " read less"}
      </span>
      </p>
    );
    };
      
    
function WrapInstructorCards(){

  const hide = {
    // Adding media querry..
    '@media (max-width: 1150px)': {
      display: 'none',
    },
  };    
  const mobilehide = {
    // Adding media querry..
    '@media (min-width: 1149px)': {
        display: 'none',
    },
  };
return(
        <StyleRoot>
        <div style={{marginTop:'60px' }} >
            <div style={hide}  >
                <h5 className="header">Meet our Instructors</h5>
                <p className="smalltext col-7" style={{margin:'30px 0px 0px 0px'}}  >On a day to day basis, 
                            we meet with CXOs of companies in the likes of Robert Bosch, 
                            Tata Motors, Mercedes Benz and much more to understand their 
                            future talent requirements. These requirements are then converted 
                            into programs through our 800+ global insturctor network.
                </p>
                <p  style={{color:'#0d55cf',textAlign:'left',fontSize:'21px',fontWeight:'700',marginTop:"30px",marginBottom:'30px'}}>INSTRUCTORS PROFILES</p>
                <div style={{justifyContent:'left',width: '100%',marginTop:'10px'}}>
                    <Box 
                    display="flex"
                    flexDirection="row"
                    alignItems="flex-start"
                    
                    sx={{ maxWidth: 300, height: 200 }}>
                        <Box ml={0}  >
                        <InstructorCards />
                        </Box>
                        <div style={hide} >
                        <Box  ml={4}  >
                        <InstructorCards/>
                        </Box>
                        </div>
                    </Box>           
                  </div>
            </div>
            <div style={mobilehide}  >
                <h5 className="header" style={{color:'#0d55cf',}}>Meet our Instructors</h5>
                <p className="smalltext col-12" style={{margin:'30px 0px 30px 0px'}}  >
                <ReadMore  >

                            On a day to day basis, 
                            we meet with CXOs of companies in the likes of Robert Bosch, 
                            Tata Motors, Mercedes Benz and much more to understand their 
                            future talent requirements. These requirements are then converted 
                            into programs through our 800+ global insturctor network.
                </ReadMore  >
                </p>
                <div  className="wrapper"> 
                            <div  className="item">
                            <InstructorCards />  
                            </div>
                            <div  className="item">
                            <InstructorCards />   
                            </div>
                    </div>
            
            </div>

        </div>
        </StyleRoot>

    );
}

export default WrapInstructorCards