import React, { useEffect, useState } from 'react';
import Radium, { StyleRoot } from 'radium';

const ReadMore = ({ children }) => {
    const text = children;
    const [isReadMore, setIsReadMore] = useState(true);
    const toggleReadMore = () => {
      setIsReadMore(!isReadMore);
    };
    return (
      <p style={{marginBottom:'0px'}} className="text">
      {isReadMore ? text.slice(0, 180) : text}
      <span style={{cursor: "pointer",color: "#0d55cf",'&:hover': {
                                  color: '#0d47a1',
                              }
  }} onClick={toggleReadMore} className="read-or-hide">
        
        {isReadMore ? "...read more" : " read less"}
      </span>
      </p>
    );
    };
      

const Curriculam = () => {
    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };
    const hide = {
        // Adding media querry..
        '@media (max-width: 1150px)': {
          display: 'none',
        },
      };    
      const mobilehide = {
        // Adding media querry..
        '@media (min-width: 1149px)': {
            display: 'none',
        },
      };


    return (
        <StyleRoot>
        <>
            {(width <= 1200) ?
                <div className="row">
                <div style={hide}  >
                <h5 className="header" style={{margin:'60px 0px 0px 0px'}}>Curriculum Design and Projects</h5>
                <p className="smalltext col-7" style={{margin:'30px 0px 0px 0px'}}  >
                                A 6 months program which gives students access 
                                to the courses at our state of the art skill centre amnd 
                                additioinal courses thyat can be taken in an online mode to 
                                guide them on their journey into datascience and data Analytics. 
                                laptops can be availl at the additional cost of 18K                </p>
            </div>
            <div style={mobilehide}  >
                <h5 className="header" style={{color:'#0d55cf',margin:'60px 0px 0px 0px'}}>Curriculum Design and Projects</h5>
                <p className="smalltext col-12" style={{margin:'30px 0px 30px 0px'}}  >
                <ReadMore  >

                                A 6 months program which gives students access 
                                to the courses at our state of the art skill centre amnd 
                                additioinal courses thyat can be taken in an online mode to 
                                guide them on their journey into datascience and data Analytics. 
                                laptops can be availl at the additional cost of 18K               
                 </ReadMore  >
                </p>
            </div>

                </div> 
            :
                <div className="row">
                <div className="col-md-6 ">
                    <div className="text_left numerical_text">Curriculum Design and Projects</div>
                    <div className="text_paragraph text_left">
                            A 6 months program which gives students access to the 
                            courses at our state of the art skill centre amnd additioinal courses 
                            thyat can be taken in an online mode to guide them on their journey into datascience and data Analytics.
                            laptops can be availl at the additional cost of 18K
                    </div>
                </div>
            </div> }
        </>
        </StyleRoot>
    )
}
export default Curriculam;