import React, { useEffect, useState } from 'react';
import BlueOval from '../Assets/blue_oval.png';
import YellowOval from '../Assets/yellow_oval.png';
import MobData from '../Assets/mob_data.png';
import Mobile from '../Assets/mobile.png';


const Courses = () => {

    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };

    return (
        <>
            {(width <= 1150) ?
                <>
                    <div className="row">
                        <div className="text_topheading text_left">Job Leading Engineering Courses</div>
                        <div className="col-md-6">
                            <div className="" style={{marginBottom:'30px'}}>
                                <div className="text_small text_clr text_left" style={{marginTop:'10px'}}>Enroll a course, complete it and get a job</div>
                                <div className="row" style={{marginTop:'30px'}}>
                                    <div className="col-1 text_left my-2">
                                        <button className="icon_btn icon_clr_home" style={{textAlign:'center'}}>#1</button>
                                    </div>
                                    <div className="col-11">
                                        <div className="text_midhead text_left text_pad">100 Placements every month</div>
                                        <div className="text_small text_left text_pad">3 Interviews everyday</div>
                                    </div>
                                </div>
                                <div className="col-md-8 mt-4"><hr className="divide"/></div>

                                <div className="row "style={{marginTop:'30px',}}>
                                    <div className="col-1 text_left my-2">
                                        <button className="icon_btn icon_clr_home">#2</button>
                                    </div>
                                    <div className="col-11 ">
                                        <div className="text_midhead text_left text_pad">55Lakhs per annum</div>
                                        <div className="text_small text_left text_pad">Highest CTC (International Placements)</div>
                                    </div>
                                </div>
                                {/* <div className="mt-3 col-md-8"><hr className="divide"/></div> */}

                            </div>
                            
                            <div className=" col-md-8"><hr className="divide"/></div>
                            
                            <div className="text_midhead text_blueClr text_left">Get your dream job today!</div>
                            <div className="text_left col-md-5" style={{marginTop:'10px'}}>

                            <div class="input_control">
                                            <div class="textfield_align">
                                            <select style={{width:'70px',backgroundColor:'transparent'}} className="country_field">
                                                <option value="+91">+91</option>
                                                <option value="+44">+44</option>
                                            </select>                                            
                                                </div>
                                                <div class="textfield_align">
                                                    <input className="num_field" placeholder="Enter the mobile number" type="text" />
                                                    <label className="label_style">Phone</label>
                                                </div>
                                            </div>
                                            </div>

                            <div className="text_left mt-3"><button className="btn btn_demohome input_widths" >Free Demo Class</button></div>
    </div>
                        <hr style={{marginTop:'30px',marginBottom:'0px'}}className="divide"/>

                    </div>
                </> :
              
              <>
                           <>
            <div className="row ">
                <div className="col-6">
                <div className="text_topheading text_left">Job Leading Engineering Courses</div>
                    <div className=""style={{marginBottom:'30px'}}>
                    <div className="text_small text_clr text_left" style={{marginTop:'10px'}}>Enroll a course, complete it and get a job</div>
                    <div className="row " style={{marginTop:'30px'}}>
                        <div className="col-1 text_left my-2">
                            <button className="icon_btn icon_clr_home">#1</button>
                        </div>
                        <div className="col-8">
                            <div className="text_midhead text_left">100 Placements every month</div>
                            <div className="text_small text_left ">3 Interviews everyday</div>
                        </div>
                    </div>
                    <div className="row" style={{marginTop:'30px',}} >
                        <div className="col-1 text_left my-2">
                            <button className="icon_btn icon_clr_home">#2</button>
                        </div>
                        <div className="col-8 ">
                            <div className="text_midhead text_left ">55Lakhs per annum</div>
                            <div className="text_small text_left ">Highest CTC (International Placements)</div>
                        </div>
                    </div>
                    </div>
                    <div className=" col-md-8"><hr className="divide"/></div>
                    <div className="text_midhead text_blueClr text_left">Get your dream job today!</div>
                    <div className="text_left col-md-5" style={{marginTop:'10px'}}>

                    <div class="input_control">
                                    <div class="textfield_align">
                                    <select style={{width:'70px',backgroundColor:'transparent'}} className="country_field">
                                                <option value="+91">+91</option>
                                                <option value="+44">+44</option>
                                            </select>                                            
                                        </div>
                                        <div class="textfield_align">
                                            <input className="num_field" placeholder="Enter the mobile number" type="text" />
                                            <label className="label_style">Phone</label>
                                        </div>
                                    </div>
                                    </div>

                <div className="text_left mt-3"><button className="btn btn_demohome input_widths">Free Demo Class</button></div>
                </div>
                <div className="col-6 ">
                <img className="rounded float "style={{  backgroundColor: 'transparent',
                                                                                height: '450px',
                                                                                marginTop: '110px',
                                                                                position:'absolute',
                                                                                marginLeft: '-120px',
                                                                                width: '243px',zIndex:'3'
                                                                                }} src={Mobile} alt="" />
                    <img className="rounded float" style={{  backgroundColor: 'transparent',
                                                                                height: '442px',
                                                                                marginTop: '120px',
                                                                                position:'absolute',
                                                                                marginLeft: '-105px',
                                                                                width: '212px',zIndex:'2'
                                                                                }} src={MobData} alt="" />

                    <img className="rounded float-end" style={{  backgroundColor: 'transparent',
                                                                                height: '250px',
                                                                                opacity: '0.3',
                                                                                top: '113px',
                                                                                width: '253px',
                                                                                left: '1010px',
                                                                                }} src={YellowOval} alt="" />
                    <img className="rounded float-start" style={{  backgroundColor: 'transparent',
                                                                                height: '250px',
                                                                                opacity: '0.3',
                                                                                marginTop: '310px',
                                                                                marginLeft: '0px',
                                                                                width: '253px',
                                                                                transform: 'rotate(180deg)',
                                                                                }} src={BlueOval} alt="" />
                </div>
                <div style={{margin:'0px,10px'}}><hr className="divide"/></div>

            </div>
        </>
                </>}
        </>
    )
}
export default Courses;