import React, { useEffect, useState } from 'react';
import SupportCard from './SupportCard';

const Support = () => {

    const [width, setWidth] = useState(window.innerWidth);

    useEffect(() => {
        window.addEventListener('resize', handleWindowSizeChange);
    });

    const handleWindowSizeChange = () => {
        setWidth(window.innerWidth);
    };
    
    return (
        <>  
        {(width <= 1150) ?             
                             <div  className="wrapper"> 
                                <div  className="item">
                                <SupportCard  color='#44d7b6' num='1'/>
                                </div>
                                <div  className="item">
                                <SupportCard color='#6dd400' num='2'/>
                                </div>
                                <div  className="item">
                                <SupportCard color='#0d55cf' num='3'/>                
                                </div>
                             </div>
            :
                        <div className="row">
                            <div className="col" style={{marginRight:'10px',minwidth:'50px'}}>
                            <SupportCard  color='#44d7b6' num='1'/>
                            </div>
                            <div className="col" style={{marginRight:'10px',minwidth:'50px'}}>
                            <SupportCard color='#6dd400' num='2'/>
                            </div >
                            <div className="col" style={{marginRight:'10px',minwidth:'50px'}}>
                            <SupportCard color='#0d55cf' num='3'/>                
                            </div>
            </div> }
        </>
    )
}
export default Support;